@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans Condensed', sans-serif;
  color: dimgrey;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ServicesPage {
  padding: 20px;
  min-width: 30%;
}

.ServiceList {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100px;
  margin-top: 20px;
  border: 1px solid #c4c4c5;
}

.ServiceWrapperValue {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.ServiceItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 100%;
  border-bottom: 1px solid #c4c4c5;
  padding: 10px;
}

.ServiceItem:nth-last-child(1) {
  border-bottom: none;
}

.ServiceButton {
  border: none;
  background-color: #db3948;
  text-decoration: none;
  font-size: 1rem;
  color: white;
  padding: 7px 9px;
  margin-left: 20px;
  cursor: pointer;
}

.EditServicePage {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 30%;
  min-height: 100px;
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #c4c4c5;
}

.EditServiceForm {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.EditServiceForm > input {
  border: 1px solid #c4c4c5;
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 10px;
}

.ButtonsBlock {
  display: flex;
  justify-content: flex-end;
  padding-top: 10px;
  padding-bottom: 10px;
}

.CancelButton {
  margin-right: 15px;
}

.CancelButton, .ChangeButton {
  border: none;
  background-color: #db3948;
  padding: 10px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1rem;
  color: white;
}
